<template>
  <router-link v-if="suffix" :to="'/match/'+match.id">
    <el-row class="match-line-wrapper">
      <el-col :span="10">
        <div class="match-line-left" :class="[prefix||inProgress?'space-between-left':'']">
          <span style="font-weight: 400" v-if="prefix||inProgress"> {{inProgress?'LIVE': new Date(match.startDate).toLocaleDateString($owl.locale,{ day: '2-digit', month: '2-digit' })}}</span>
          <router-link :to="'/team/'+match.competitors[0].id+'/'+match.competitors[0].name" class="team-name-score">
            <span :class="[concluded?leftWin?'':'normal-font':'normal-font']">{{showFull? match.competitors[0].name:match.competitors[0].abbreviatedName}}</span>
            <img :src="match.competitors[0].logo.main?match.competitors[0].logo.main.svg:match.competitors[0].logo"
                 class="team-logo-standing" :alt="match.competitors[0].name">
          </router-link>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="match-score" v-if="concluded">
        <span class="score-span" :class="[concluded?leftWin?'win':'lose':'']">
        {{match.scores[0].value}}
      </span>
          <span class="score-span" style="width: 1rem">
      </span>
          <span class="score-span" :class="[concluded?leftWin?'lose':'win':'']">
        {{match.scores[1].value}}
      </span>
        </div>
        <div class="live-score" v-else-if="inProgress">
      <span class="score-span">
        {{match.scores[0].value}}
      </span>
          <span class="score-span" style="width: 1rem">
      </span>
          <span class="score-span">
        {{match.scores[1].value}}
      </span>
        </div>
        <div v-else class="match-time">
          {{new Date(match.startDate).toLocaleTimeString($owl.locale,{hour: '2-digit', minute:'2-digit',hour12:false})}}
        </div>

      </el-col>
      <el-col :span="10">
        <div class="match-line-right" :class="[suffix?'space-between-right':'']">
          <router-link :to="'/team/'+match.competitors[1].id+'/'+match.competitors[1].name" class="team-name-score">
            <img :src="match.competitors[1].logo.main?match.competitors[1].logo.main.svg:match.competitors[1].logo"
                 class="team-logo-standing" :alt="match.competitors[1].name">
            <span :class="[concluded?leftWin?'normal-font':'':'normal-font']">{{showFull? match.competitors[1].name:match.competitors[1].abbreviatedName}}</span>
          </router-link>
          <router-link v-if="suffix" :to="'/match/'+match.id"><i class="el-icon-arrow-right"></i></router-link>
        </div>
      </el-col>
    </el-row>
  </router-link>

</template>

<script>
  export default {
    name: "MatchBrief",
    props: ['match', 'prefix', 'suffix'],
    computed: {
      showFull() {
        return this.$store.getters.getWindowParam.width > 768;
      },
      leftWin() {
        return this.match.scores[0].value > this.match.scores[1].value
      },
      concluded() {
        return this.match.state === 'CONCLUDED'
      },
      inProgress() {
        return this.match.state === "IN_PROGRESS"
      }
    }
  }
</script>

<style scoped>
  .match-line-wrapper {
    padding: 1rem 0;
    border-bottom: 1px solid rgba(51, 51, 51, .2);
    font-weight: 700;
    font-size: 1rem;
  }


  .match-line-left {
    display: flex;
    justify-content: flex-end;
    align-items: center;

  }

  .match-line-right {
    display: flex;
    align-items: center;
  }

  .match-score {
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;

  }

  .live-score {
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    background-color: #e50e47;
    color: white;
    padding: 0.2rem;
    width: 80%;
    margin: 0 auto;


  }

  .match-time {
    height: 1.6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    background-color: #f3f3f3;
    padding: 0.2rem;
    width: 80%;
    margin: 0 auto;

  }

  .win {
    color: green;
  }

  .normal-font {
    font-weight: 400;
  }

  .lose {
    color: red;
  }

  .team-name-score {
    display: flex;
    align-items: center;
  }

  .space-between-left {
    justify-content: space-between;
    margin-left: 1rem;
  }

  .space-between-right {
    justify-content: space-between;
    margin-right: 1rem;
  }

  .team-logo-standing {
    width: 1.6rem;
    height: 1.6rem;
    margin: 0 0.3rem;
  }

  @media (max-width: 768px) {
    .match-line-wrapper {
      font-size: 0.9rem;
    }

    .space-between-left {
      margin-left: .5rem;
    }

    .space-between-right {
      margin-right: .5rem;
    }

  }

</style>



