<template>
  <div class="matches">
    <el-tabs v-if="stages.length>0" type="border-card" v-model="currentStage[0]" class="tab-outside">
      <el-tab-pane :label="meta.strings['owl.schedule.stage.'+stage.slug]" :name="stage.id+''" v-for="stage in stages"
                   :key="stage.id" lazy>
        <el-tabs type="border-card" v-model="currentStage[1]" class="tab-inside">
          <el-tab-pane :label="'第'+(week.id+1)+'周'" :name="week.id+''"
                       v-for="week in stage.weeks"
                       :key="week.id" lazy>
            <div v-for="day in week.days" class="matches-day">
              <div class="date-indicator">
                {{day.startDay}} {{new Date(day.startDay).toLocaleString($owl.locale,{weekday: 'long'})}}
              </div>
              <match-brief :match="match" v-for="match in day.matches" :key="match.id" :suffix="true"></match-brief>
            </div>
          </el-tab-pane>

        </el-tabs>
      </el-tab-pane>

    </el-tabs>
    <div v-loading="stages.length===0" style="min-height: 300px" v-if="stages.length===0"></div>
  </div>
</template>

<script>
  import MatchBrief from "@/components/Matches/MatchBrief";

  export default {
    name: "Schedule",
    components: {MatchBrief},
    data() {
      return {
        stages: [],
        totalData: {},
        meta: {}
      }
    },
    methods: {
      fetchMatches() {
        this.stages = [];
        this.$owl.schedule({season: this.season}).then(({stages, meta}) => {
          this.stages = stages;
          this.totalData[this.season] = stages;
          this.meta = meta;
        })
      }
    },
    mounted() {
      this.fetchMatches();
    },
    watch: {
      season: function () {
        if (this.totalData[this.season] === undefined)
          this.fetchMatches();
        else {
          this.stages = this.totalData[this.season];
        }

      }
    },
    computed: {
      currentStage() {
        const now = new Date().getTime();
        for (let stage of this.stages) {
          for (let week of stage.weeks) {
            if (now > week.startDate && now < week.endDate)
              return [stage.id + '', week.id + '']
          }
        }
        return [this.stages[0].id + '', this.stages[0].weeks[0].id + '']
      }
      ,
      season() {
        return this.$store.getters.getSeason
      },
    }
  }
</script>

<style>
  .matches-day {
    margin-bottom: 20px;
    background-color: white;
    border: 1px solid rgba(51, 51, 51, .2);
    border-bottom: none;
  }

  .date-indicator {
    border-bottom: 1px solid rgba(51, 51, 51, .2);
    line-height: 3;
  }

  .matches .tab-inside .el-tabs__content {
    background-color: #f8f8f8;
  }
</style>



